  .category-headline{
    font-family: $karla;
    font-size: 50px;
    font-weight: 600;
    text-align: center;
    // margin-top: 80px;
    color: $header-bg;
    text-transform: capitalize;
}

.article-headline{
    font-family: $karla;
    font-size: 50px;
    font-weight: 600;
    text-align: center;
    // margin-top: 80px;
    color: $header-bg;
    text-transform: capitalize;
}

.aboutMe{
    display: block;

    &__inner{
        display: grid;
        grid-gap: 50px;
        grid-template-columns: 1fr 1fr;
        justify-items: center;
    }

    &__greeting{
        font-family: $karla;
        font-size: 40px;
        font-weight: 600;
        color: #000;
        align-self: center;
        justify-self: center;
        text-align: center;
    }

    &__name{
        display: block;
        color: $color-point;
        margin-top: 15px;
    }

    &__text{
        font-family: $karla;
        font-size: 28px;
        font-weight: 600;
        color: #000;
        grid-column: span 2;
    }

    &__text--italic{
        font-style: italic;
        font-size: 34px;
    }

    &__text--highlighted{
        color: $color-point;
    }

    &__text--bold{
        font-weight: 700;
    }

    &__img{
        width: 300px;
        height: 300px;

        // background-image: url(../../img/img-man.png);
        background-repeat: no-repeat;
        background-size: cover;
    }

    .social{
        grid-column: span 2;
        justify-self: center;
    }
}


.contact{
    display: block;
    position: relative;

    &__inner{
        display: block;
    }

    &__main-title{
        padding-bottom: 40px;
        text-transform: uppercase;
        font-family: $karla;
        font-size: 38px;
        font-weight: 700;
        text-align: center;
    }

    &__list{
        display: flex;
        justify-content: space-between;
    }

    &__item{
        width: 350px;
        display: flex;
        flex-direction: column;
    }

    &__title{
        color: #fff;
        font-family: $karla;
        font-size: 22px;
        line-height: 32px;
        position: relative;
        margin: 0 0 40px 0;
        background: $header-bg;
        border-left: 5px solid $color-point;
        padding: 8px 0 7px 17px;
    }

    &__form{
        display: flex;
        flex-direction: column;
    }

    &__input{
        width: 90%;
        height: 35px;
        margin-bottom: 20px;
        padding-left: 10px;
        font-size: 16px;
        font-family: $karla;
    }

    &__input--mess{
        padding-top: 8px;
        height: 120px;
        resize: none;
    }

    &__btn{
        width: 140px;
        height: 40px;
        background-color: $header-bg;
        font-size: 16px;
        font-family: $karla;
        color: #fff;
        border: 2px solid $color-point;
        cursor: pointer;
    }

    &__map{
        margin: 0 auto;
        width: 300px;
        height: 270px;
    }

    &__desc{
        display: grid;
        grid-gap: 15px;
        margin-bottom: 30px;
    }

    &__desc-item{
        font-size: 16px;
        font-family: $karla;
    }

    &__desc-item--bold{
        font-weight: 700;
        margin-right: 10px;
    }

    .social{
        width: 200px;
    }
}
  
  
  
  @media (min-width: 800px) and (max-width: 1169px) {
    .articles {
      &__title {
        width: 90%;
  
        grid-gap: 16px;
      }
  
      &__title-tags {
        grid-gap: 10px;
        grid-template-columns: repeat(2, 150px);
        grid-template-rows: 20px 20px;
      }
  
      &__title-headline {
        margin-left: 10px;
  
        font-size: 20px;
      }
  
      .date-articles {
        width: 70px;
        height: 70px;
        margin-right: 10px;
  
        &__day {
          padding-top: 19px;
  
          font-size: 20px;
        }
  
        &__month {
          font-size: 8px;
        }
      }
    }
  }
  @media (min-width: 600px) and (max-width: 799px) {
    .articles {
      &__title {
        width: 90%;
        height: 115px;
  
        grid-gap: 20px;
      }
  
      &__title-tags {
        font-size: 12px;
  
        grid-gap: 10px 20px;
        grid-template-columns: repeat(2, 110px);
        grid-template-rows: 20px 20px;
      }
  
  
  
      &__title-headline {
        margin-left: 5px;
  
        font-size: 16px;
      }
    }
  
    .date-articles {
      width: 70px;
      height: 70px;
  
  
      &__day {
        font-size: 20px;
      }
  
      &__month {
        font-size: 10px;
      }
    }
  
    .social {
      grid-template-columns: 1fr 1fr;
  
      &__item {
        &:nth-child(n+5):nth-child(n) {
          display: none;
        }
      }
    }
  
  
    .btn {
      align-self: center;
    }
  
  
    .articles-sm .date-articles {
      left: 2%;
    }
    .advertisement {
      &__block-text {
        font-size: 22px;
      }
    }
  }
  
